<template>
  <div>
   <nav-bar title="正文"></nav-bar>
   <div class="detail-container">
   <h3 class="title">大标题</h3>
    <!-- 子标题 -->
    <p class="subtitle">
      <span>发表时间：2018-7-21</span>
      <span>点击：10次</span>
    </p>
    <hr>
    <div class="content" v-html="'很多的内容'"></div>
   </div>
  </div>
</template>

<script>
import navBar from '@/components/common/navBar'
export default {
  components: {
    navBar
  },
  data () {
    return {
      id: this.$route.query.id
    }
  },
  mounted () {
    console.log(this.id)
  }
}
</script>

<style scoped lang="scss">
.detail-container{
    padding: 0 5px;
    .title{
      font-size: 16px;
    }
    .subtitle{
        display: flex;
        justify-content: space-between;
        font-size: 13px;
        color: #222;
    }
    .content{
        img{
            width: 100%;
        }
    }
}
</style>



